const mapStateToProps = (state, ownProps) => {
  console.log(
    "state filters :: " + JSON.stringify(state.categoryPage.category.filters)
  );
  return {
    productList: state.categoryPage.category.productList,
    filters: state.categoryPage.category.filters,
    category_seo: state.categoryPage.category.category_seo,
    categories: state.categoryPage.category.categories,
    // selectedFilter: state.categoryPage.category.selectedFilter
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      getCategoryProducts: Actions.getCategoryProducts,
      getCategoryProductsByFilters: Actions.getCategoryProductsByFilters,
      getFilterCollection: Actions.getFilterCollection,
      getCategorySEO: Actions.getCategorySEO,
      handleSortProduct: Actions.handleSortProduct,
    },
    dispatch
  );
};

export default withReducer(
  "categoryPage",
  reducer
)(connect(mapStateToProps, mapDispatchToProps)(CategoryPage));
