import React, { Component } from "react";
import _ from "lodash";

class FilterSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
    };
  }

  // componentWillMount(){
  //     this.setState({items: this.props.item.Filters})
  // }

  static getDerivedStateFromProps(props, state) {
    console.log("Search Filter  :: " + props.selectedFilter);
    console.log("Item Filter  :: " + props.item.Filters);
    return {
      selectedFilter: props.selectedFilter,
      items: props.item.Filters,
      prefix: props.prefix,
    };
  }

  filterList = (event) => {
    var updatedList = this.props.item.Filters;

    this.props.updateFilterList(event);

    updatedList = updatedList.filter(function (item) {
      return item.toLowerCase().search(event.target.value.toLowerCase()) !== -1;
    });
    // alert('Updated list :: ' + JSON.stringify(updatedList))
    this.setState({ items: updatedList }, () => {
      console.log("ITems ::: " + JSON.stringify(this.state.items));
    });
  };

  handleFilters = (event) => {
    console.log("Unboxing :: " + event.target.getAttribute("itemId"));

    let filter = {
      name: event.target.getAttribute("itemId").split("^")[0],
      value: event.target.getAttribute("itemId").split("^")[1],
    };

    // console.log('Unboxing filter :: ' + JSON.stringify(filter));

    this.props.handleFilters(filter, this.props.tag, this.props.p_cat);
  };

  render() {
    return (
      <>
        {/*<div className="search">
            <input type="text" placeholder={`Search By ${this.props.name}`} onChange={this.filterList}/>
            <img class="search-button" src="/assets/images/search-icon.png" alt="search" />
		</div>*/}
        <ul className="checkbox">
          {this.state.selectedFilter &&
            console.log(
              "Type Filter :: " + JSON.stringify(this.state.selectedFilter)
            )}
          {this.state.prefix && console.log("Prefix :: " + this.state.prefix)}
          {this.state.items.map(
            (item, i) => (
              <li
                key={i}
                onClick={this.handleFilters}
                //   className={(this.state.prefix && this.state.prefix === 'all') ? 'active'
                //   : this.state.selectedFilter && (_.find(this.state.selectedFilter, (filter) => {
                //     return _.find(filter.value, (val) => { return val === item})
                // })) ? 'active' : ''}
                className={
                  this.state.prefix && this.state.prefix === "all"
                    ? "active"
                    : this.state.selectedFilter &&
                      _.find(this.state.selectedFilter.value, (index) => {
                        return index === item;
                      })
                    ? "active"
                    : ""
                }
                itemId={`${this.props.name}^${item}`}
              >
                {item}{" "}
              </li>
            )
            // ({item.total})</li>
          )}
        </ul>
      </>
    );
  }
}

export default FilterSearch;
